import Boom from '@hapi/boom';
import services from '../services/index';

export default async function addFavorites(req, res, next) {
  try {
    const { profileImage, deviceToken, active } = req.body;
    if (!profileImage && !deviceToken && active === undefined) {
      throw Boom.badRequest(
        'Atleast one of profileImage/deviceToken/active is mandatory'
      );
    }
    return res.json(
      await services.updateUserDetails(
        req.params.id,
        profileImage,
        deviceToken,
        active
      )
    );
  } catch (err) {
    return next(err);
  }
}
